import React from 'react'
// import x from "../imge/download.jfif"
import x from '../imge/df012ba7-7e28-41e0-a438-3b71fbd9aa67.jpg'
export default function About() {
  return (
    <>
    
    
    <section id="section-about" className="about-section my-5">
      <h1 className=' my-4'>About</h1>
      <div className="all-sections">
        <div data-aos="fade-right" className="mini-section">
          <div className="hexagon">
            <i className="fa fa-fighter-jet" aria-hidden="true"></i>
          </div>
          <div className="text-mini-section">
            <h1>Fast</h1>
            <p>
              Fast load times and lag free interaction, my highest priority.
            </p>
          </div>
        </div>
        <div data-aos="fade-up" className="mini-section">
          <div className="hexagon">
            <i className="fa fa-window-restore" aria-hidden="true"></i>
          </div>
          <div className="text-mini-section">
            <h1>Responsive</h1>
            <p>My layouts will work on any device, big or small.</p>
          </div>
        </div>
        <div data-aos="fade-up" className="mini-section">
          <div className="hexagon">
            <i className="fa fa-rocket" aria-hidden="true"></i>
          </div>
          <div className="text-mini-section">
            <h1>Intuitive</h1>
            <p>Strong preference for easy to use, intuitive UX/UI.</p>
          </div>
        </div>
        <div data-aos="fade-left" className="mini-section">
          <div className="hexagon">
            <i className="fa fa-bolt" aria-hidden="true"></i>
          </div>
          <div className="text-mini-section">
            <h1>Dynamic</h1>
            <p>
              Websites don't have to be static, I love making pages come to life
            </p>
          </div>
        </div>
      </div>
      <div className="who-am-i">
        <div data-aos="zoom-in-up" className="image-me">
            <img src={x} className="w-100" alt="" />
        </div>

        <div className="all-who">
          <h1 data-aos="zoom-in-down">Who am I?</h1>
          <div className="div-para">
            <p data-aos="zoom-in-down">
              Hello everyone, my name is Hussein Farhat and I am a Front End
              Developer from Egypt Cairo. I am self-taught developer,
              passionate and very fast learner. I have learned how to program
              through various resources, currently I am learning on
               codezone. I am eager to learn and grow - Everyday. My ambition
              is to make awesome websites, not just good enough.
            </p>
          </div>
          <button data-aos="zoom-in-down" className="hvr-bounce-to-right rounded-3">
            Download CV
          </button>
        </div>
      </div>
    </section>
    
    </>
  )
}
